* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Open Sans", "Raleway", "Helvtica", sans-serif;
  background: #111;
  color: #fff;
}

.App{
  width: 100vw;
}

form {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 2rem auto;
  padding: 0 0.5rem;
}

form input {
  text-align: center;
  font-size: 1.5rem;
  flex: 9;
  outline: none;
  border: none;
  height: 3rem;
  max-width: 100%;
}

form input:focus {
  box-shadow: 0px 0px 10px #fff;
}

form button {
  padding: 0 1rem;
  font-size: 1.5rem;
  color: #333;
  border-radius: 3px;
  flex: 1;
  border: none;
  background: burlywood;
  min-height: 3rem;
}

button:hover {
  transform: scale(0.9);
  filter: contrast(1.5);
}

ul {
  border-radius: 5px;
  max-width: 800px;
  width: 80%;
  margin: 2rem auto;
  background: #333;
  min-width: 230px;

}

.listItemsContainer {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.listItemsContainer input {
  align-self: center;
  flex: 1;
  height: 1rem;
  margin-left: 1rem;
}

.listItemsContainer li {
  padding: 1rem;
  padding-bottom: 0;
  flex: 9;
  font-weight: 100;
  list-style: none;
  margin-bottom: 1rem;
  border-bottom: 1px solid white;
}

.listItemsContainer button:not([type="submit"]) {
  margin: .3rem;
  margin-right: 1rem;
  flex: 1;
  color: #fff;
  background: rgba(255, 0, 0, 0.2);
  outline: none;
  display: block;
  border: 1px solid rgb(116, 34, 34);
  max-width: 25px;
  height: 25px;
}
